.home-cover::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("/public/home-cover.jpg");
  background-size: cover;
  z-index: -1;
  opacity: 0.5;
  backdrop-filter: blur(8px);
}
